.user-profile {
    height: 100dvh;
    background: linear-gradient(to bottom right, #84cfe8, #c9ecf4);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .user-profile .profile {
    width: 60%;
    background-color: #fff;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.2);
    display: flex;
  }
  .profile-left {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    border-right: 1px solid #dee2e6;
  }
  .profile-left i {
    font-size: 50px;
    margin-top: 50px;
  }
  .profile-left h4 {
    font-size: 24px;
    font-weight: 600;
    color: transparent;
    background: linear-gradient(to right, #007991, #4286f4);
    background-clip: text;
  }
  .profile-right {
    flex: 2;
    padding: 20px;
  
    text-align: end;
  }
  .profile-right .user-data {
    display: flex;
    text-align: start;
  }
  .profile-right .user-data-text {
    flex: 1;
  }
  .profile-right .user-data-text span {
    font-weight: 700;
    font-size: 16px;
    color: #343a40;
  }
  .profile-right .user-data-text p {
    font-weight: 400;
    color: #495057;
  }
  .profile-right .user-data-image {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: end;
    justify-content: space-between;
  }
  .user-data-image .upload {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: #2d9197;
    padding: 10px 20px;
    border-radius: 10px;
  }
  .profile-right .user-data-image .image {
    border-radius: 50%;
    width: 70%;
    height: 100%;
  }
  .profile-right .user-data-image .image img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    cursor: pointer;
  }
  .profile-right .user-data-image i {
    font-size: 70px;
    color: #2d9197;
    cursor: pointer;
  }
  .profile-right .user-data-image p {
    font-size: 16px;
    color: #fff;
    font-weight: 600;
    text-transform: uppercase;
    margin: 0;  
  }
  .profile-right button {
    border: none;
    background: #2d9197;
    width: 150px;
    height: 40px;
    border-radius: 10px;
    font-weight: 600;
    font-size: 16px;
    color: #fff;
    cursor: pointer;
  }
  /* media */
  
  @media only screen and (max-width: 1280px) {
    .user-profile .profile {
      width: 80%;
      padding: 20px;
      height: 70%;
      position: relative;
    }
    .profile-right .user-data-image .image {
      width: 60%;
    }
    .profile-right button {
      position: absolute;
      bottom: 0;
      left: 0;
      margin: 0 0 20px 20px;
    }
  }
  @media only screen and (max-width: 1100px) {
    .profile-left i {
      font-size: 50px;
      margin-top: 20px;
    }
  
    .profile {
      height: auto !important;
    }
  
    .profile-right .user-data-text {
      flex: 1.5;
    }
    .profile-right .user-data-image .image {
      width: 100%;
    }
    .profile-right .user-data-image i {
      font-size: 40px;
    }
  }
  @media only screen and (max-width: 800px) {
    .user-profile .profile {
      flex-direction: column;
      width: 90%;
    }
    .profile-left {
      flex-direction: row;
      align-items: center;
      width: 100% !important;
      border-right: none;
      padding: 10px;
      border-bottom: 1px solid #dee2e6;
    }
    .profile-left i {
      font-size: 40px;
      margin-top: 0;
    }
    .profile-right button {
      position: absolute;
      top: 0;
      right: 0;
      left: unset;
      margin: 20px 20px 0 0;
    }
    .profile-right .user-data-image .image {
      width: 80%;
    }
  }
  @media only screen and (max-width: 580px) {
    .user-profile .profile {
      height: 80% !important;
    }
    .profile-left {
      flex: 0;
    }
    .profile-right button {
      position: absolute;
      top: 0;
      right: 0;
      left: unset;
      margin: 10px 10px 0 0;
      padding: 5px 15px;
      width: 100px;
      height: 30px;
      font-size: 14px;
      font-weight: 400;
    }
    .user-profile .profile {
      padding: 10px;
    }
    .profile-left h4 {
      font-size: 16px;
    }
    .profile-left i {
      font-size: 20px;
    }
    .profile-right {
      padding: 5px;
  
      text-align: end;
    }
    .profile-right h2 {
      font-size: 20px;
    }
    .profile-right .user-data {
      display: flex;
      flex-direction: column;
    }
    .profile-right .user-data-image {
      flex-direction: row;
    }
    .profile-right .user-data-image .image {
      width: 40%;
    }
    .profile-right .user-data-image .image img {
      width: 160px;
      height: 160px;
      object-fit: contain;
    }
  }
