.model-upload {
    height: 100dvh;
    background: linear-gradient(to bottom right, #84cfe8, #c9ecf4);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.2);
    background-color: #fff;
  }
  .model-upload .model {
    width: 60%;
    background-color: #fff;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.2);
    display: flex;
  }
  .model-left {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    border-right: 1px solid #dee2e6;
  }
  .model-left button {
    font-size: 50px;
    margin-top: 50px;
  }
  .model-left h4 {
    font-size: 24px;
    font-weight: 600;
    color: transparent;
    background: linear-gradient(to right, #007991, #4286f4);
    background-clip: text;
  }
  .model-right {
    flex: 2;
    padding: 20px;
  
    text-align: center;
  }
  
  .model-right .image {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }
  
  .model-right .image img {
    border-radius: 50%;
    width: 70%;
    height: auto;
    object-fit: cover;
    cursor: pointer;
  }
  
  .model-right .upload {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 10px;
  }
  
  .model-right .upload  i {
    font-size: 50px;
    color: #2d9197;
    cursor: pointer;
  }
  
  .model-right .upload p {
    text-transform: uppercase;
    color: #343a40;
    font-size: 16px;
    font-weight: 500;
  }
  
  .model-right button {
    border: none;
    background: #2d9197;
    width: 150px;
    height: 40px;
    border-radius: 10px;
    font-weight: 600;
    font-size: 16px;
    color: #fff;
    cursor: pointer;
  }
  
  .model-right p {
    color: #495057;
    font-weight: 400;
    font-size: 19px;
  }
  
  /* media queries */
  @media only screen and (max-width: 1280px) {
    .model-upload {
      width: 80%;
      height: 70%;
      position: relative;
    }
    .model-right .image img {
      width: 60%;
    }
    .model-upload button {
      position: absolute;
      bottom: 20px;
      left: 20px;
    }
  }
  
  @media only screen and (max-width: 1100px) {
    .model-upload {
      height: auto !important;
    }
    .model-upload .upload i {
      font-size: 40px;
    }
  }
  
  @media only screen and (max-width: 800px) {
    .model-upload {
      width: 90%;
      flex-direction: column;
    }
    .model-right .image img {
      width: 80%;
    }
    .model-right button {
      position: absolute;
      top: 20px;
      right: 20px;
    }
  }
  
  @media only screen and (max-width: 580px) {
    .model-upload {
      height: 80% !important;
      padding: 10px;
    }
    .model-right .image img {
      width: 60%;
    }
    .model-upload button {
      top: 10px;
      right: 10px;
      width: 100px;
      height: 30px;
      font-size: 14px;
      padding: 5px 15px;
    }
  }
  